    --ft-neon-glow: rgba(0, 191, 255, 0.3);
    --ft-neon-blue: #00bfff; /* Standard FlexTime neon blue color (cyan-blue) */
    --ft-neon-blue-glow: rgba(0, 191, 255, 0.3);
    
    /* FlexTime Brand Typography - Legacy System */
    --ft-font-primary: 'Orbitron', monospace; /* Futuristic headers & brand */
