/* FlexTime Clean Components */
/* Metal Components */
.ft-metal-card {
    background: var(--ft-metal-surface);
    border: 1px solid var(--ft-metal-border);
    backdrop-filter: blur(20px);
    box-shadow: 0 8px 32px var(--ft-metal-shadow), inset 0 1px 0 var(--ft-metal-sheen);
    transition: all 0.3s ease;
  }
.ft-metal-card:hover {
    background: rgba(255, 255, 255, 0.08);
    border-color: rgba(255, 255, 255, 0.15);
    transform: translateY(-2px);
  }
/* Glass Components - Builder Style (Clean & Bright) */
.ft-glass-card{
  border-width: 1px;
  border-color: rgb(255 255 255 / 0.6);
  background-color: rgb(255 255 255 / 0.9);
  --tw-backdrop-blur: blur(24px);
  backdrop-filter: var(--tw-backdrop-blur) var(--tw-backdrop-brightness) var(--tw-backdrop-contrast) var(--tw-backdrop-grayscale) var(--tw-backdrop-hue-rotate) var(--tw-backdrop-invert) var(--tw-backdrop-opacity) var(--tw-backdrop-saturate) var(--tw-backdrop-sepia);
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
  animation-duration: 300ms;
}
.ft-glass-card:is(.dark *){
  border-color: rgb(255 255 255 / 0.1);
  background-color: rgb(0 0 0 / 0.4);
}
.ft-glass-card:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity, 1));
}
.ft-glass-card:hover:is(.dark *){
  background-color: rgb(0 0 0 / 0.6);
}
.ft-glass-card{
  --tw-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 10px 15px -3px var(--tw-shadow-color), 0 4px 6px -4px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
.ft-glass-card:hover{
  --tw-shadow: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 20px 25px -5px var(--tw-shadow-color), 0 8px 10px -6px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}
